* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: white;
}

body {
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0;
}

/* HEADER */

.header {
  position: absolute;
  top: 45px;
  color: black;
  display: flex;
}

 .left{
  flex-direction: column;
}


 .right{
  top:80px;
  right:4px;
  flex-direction: column;
}

.right  h5{
  font-weight: 100;

}

.header .left-information {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  margin-bottom: 10px;
}


.header .left-information > h1 {
  margin-right: 20px;
  line-height: 25px;
}

.header .left-information h5{
  color:gray;
}

.header > h5{
  color:gray;
  font-weight: 100
}

@media screen and (max-width: 679px) {
  .header .left-information {
    align-items: flex-start;
    flex-direction: column;
    margin-bottom: 0px;

  }
}

.left-menu > div {
  display: flex;
  align-items: flex-end;
}

.image-container {
  display: flex;
  justify-content: flex-start;
}

.container {
  display: flex;
  justify-content: flex-start;
  position: relative;
  list-style: none;
  color: white;
  flex-wrap: wrap;
  padding: 0;
  margin: auto;
}

@media screen and (max-width: 679px) {
  .container {
    width: 320px;
  }

  .header {
    top: 25px;
    
  }


 .right{
  top:80px;
  right:4px;
  flex-direction: column;
}


  .header .left-information h1{
    margin-bottom:7px;
  }

  .header .left-information h5{
    margin-bottom: 5px
  }


}

@media screen and (min-width: 679px) {
  .container {
    width: 644px;
  }
  .image-container:nth-child(even) {
    margin-right:4px;
  }
  
}

@media screen and (min-width: 999px) {
  .container {
    width: 972px;
  }

  .image-container:nth-child(odd) {
    margin-right:4px
  }
}

@media screen and (min-width: 1340px) {
  .container {
    width: 1296px;
  }
  .image-container:nth-child(odd) {
    margin-right:4px
  }
}

/* IMAGE */
.image-container {
  position: relative;
  overflow: hidden;
  width: 320px;
  height: 252px;
  top: 100px;
  margin-bottom:4px;

}

.image-container img {
  height: 320px;
  position: relative;
  transition: transform 3s ease 0.2s;
}

.image-container span {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  font-size: 30px;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.image-container:hover > img {
  transform: scale(1.2);
}

.image-container:hover > span {
  opacity: 1;
}

#play {
  position: absolute;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  left: 10px;
  bottom: 10px;
  z-index: 1;
  font-size: 15px;
  opacity: 1;
}
